ook at him, and with the umbrella between them, they were all dripping wet now. She shook her head no, but the man pointed, explaining that he was nearly home already. She wondered how she would get the umbrella back to him, but he said, “Take it. It&#39;s yours.” And without a backward glance, he was on his way again, head bowed to the relentless rain.<br data-v-63bd49b8><br data-v-63bd49b8> Back at home, the man&#39;s daughter met him at the door. “You&#39;re all wet,” she said, and immediately apologized like she always did, knowing how he hated the bad weather. As if she had any control over it.<br data-v-63bd49b8><br data-v-63bd49b8> The next day, when there was a break in the drizzle, the man went for another walk, and this time he got a little further before the downpour began. He bought two umbrellas from a different store clerk. It wasn&#39;t hard to find someone without. In fact, he gave up his own umbrella like he had the day before and walked home, again, in the rain. <br data-v-63bd49b8><br data-v-63bd49b8> This time when his daughter confronted him, he was wet, but smiling.<br data-v-63bd49b8><br data-v-63bd49b8> It went on like this for months, years even. He became known as the umbrella man. He learned the people&#39;s names, their backstories. They would call out to him on the street, not because they were in need of an umbrella, but because he&#39;d given them one before. He eventually had boxes of umbrellas mailed to his home so that he would have his stockpile ready before he even left the house.<br data-v-63bd49b8><br data-v-63bd49b8> His daughter would often beg him, seeing him always as a sopping mess, “Surely you have enough umbrellas,” she&#39;d say. “Keep one for yourself.” But always there was someone who needed it more. He traded even his cane in for an umbrella, then went hobbling home without, wanting only to be that little bit of shelter from someone else&#39;s storm.<br data-v-63bd49b8><br data-v-63bd49b8> At the end of his life, his 